<template>
  <div class="bg">
    <div class="nav">
      <dw-row type="flex">
        <dw-col class="item" v-for="({ name }, inx) in list" :key="name" :span="colSpan">
          <div :class="['title', { select: $route.name == name }]" @click="switchNav(name, inx)">{{ name }}</div>
        </dw-col>
      </dw-row>
      <div ref="line" class="line" :style="lineStyle"></div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [],
    };
  },
  computed: {
    colSpan() {
      return 24 / this.list.length;
    },
    lineStyle() {
      return `width:${100 / this.list.length}%`;
    },
  },
  methods: {
    switchNav(name, inx) {
      if (name == this.$route.name) return;
      this.moveTo(inx);
      this.$router.push(`/${name}`);
      // 等动画过渡途中再回到顶部
      setTimeout(() => {
        this.backTop();
      }, 200);
    },
    moveTo(inx) {
      const line = this.$refs["line"].style;
      line.left = `${(100 / this.list.length) * inx}%`;
      //   console.log(line);
    },
    backTop() {
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
    },
  },
  created() {
    // 此处简单处理下路由
    this.list = this.$router.options.routes[0].children;
    console.log("nav列表：", this.$router.options.routes[0].children);
  },
  mounted() {
    const inx = this.list.findIndex(({ name }) => name == this.$route.name);
    // console.log(this.$route.name, inx);
    const line = this.$refs["line"].style;
    line.left = `${(100 / this.list.length) * inx}%`;
  },
};
</script>

<style scoped>
.bg {
  position: sticky;
  width: 100%;
  top: 0;
  background: white;
  z-index: 9999;
}
.nav {
  position: relative;
  width: 90%;
  margin: 0 auto 2vh auto;
  padding-top: 3vh;
}
.nav .item {
  height: 8vw;
  max-height: 60px;
}
.title {
  color: #bdc3c7;
  font-size: 1.5em;
  text-align: center;
  line-height: 2em;
  cursor: pointer;
  user-select: none;
}
@media screen and (max-width: 768px) {
  .title {
    font-size: 3.2vw;
    font-weight: 700;
  }
}
.title:not(.select):hover {
  color: #66b1ff;
}
.select {
  color: #3a8ee6;
}
.line {
  /* width: 100%; */
  height: 2px;
  background: #3a8ee6;
  position: absolute;
  left: 0;
  bottom: 0;
  transition: left 0.2s ease-in-out 0s;
}
</style>
